<template>
    <div class="mb">
		<el-breadcrumb separator="/">
		  <el-breadcrumb-item v-for="v in mood" :key="v.name">{{v.meta.title}}</el-breadcrumb-item>
		</el-breadcrumb>
    </div>
</template>

<script>
    export default {
        name: "Crumbs",
        data(){
            return{
                mood:[]
            }
        },
        mounted(){
            this.mood = this.$route.matched;
        },
        watch: {
            $route: function () {
                this.mood = this.$route.matched;
            }
        }
    }
</script>

<style scoped>
    .mb{
        padding-bottom: 10px;
    }
</style>