<template>
    <div id="app">
        <el-container>
            <el-aside width="">
            <el-scrollbar style="height:100%">
                <el-menu :unique-opened="true" class="el-menu-vertical-demo" :collapse="isCollapse" background-color="#2f3e4e" text-color="#fff" :router="true" active-text-color="#409eff">
                    <div class="logo">
                        <div class="logobox" v-if="!isCollapse" style="color: #fff;text-align:center;line-height: 60px;font-size: 22px;">
                            {{Name}}
                        </div>
                    </div>
                    <el-submenu v-for="v in isshowLists" :key="v.path" :index="v.name">
                        <template  slot="title">
                            <i :class="[v.meta.icon]"></i>
                            <span slot="title">{{v.meta.title}}</span>
                        </template>
                        <el-menu-item v-for="val in v.children" :key="val.path" :disabled="val.meta.demand&&president?true:false" :index="val.name" :route="{name:val.name}"><i :class="[val.meta.icon]"></i><span>{{val.meta.title}}</span></el-menu-item>
                    </el-submenu>
                    <el-menu-item v-for="v in isshowList" :key="v.path" :index="v.name" :route="{name:v.name}">
                        <i :class="[v.meta.icon]"></i>
                        <span slot="title">{{v.meta.title}}</span>
                    </el-menu-item>
                </el-menu>
            </el-scrollbar>
            </el-aside>
            <el-container style="width: 100%">
                <el-header style="text-align: right; font-size: 12px;background: #fff;border-bottom: 1px solid #e6e6e6;">
                    <div id="fll" @click="dsplins">
                        <i class="el-icon-sanhengxian1" style="color:#333;  font-size: 25px; line-height: 50px;" ref="fll"></i>
                    </div>
					<div class="header-right">
						
						<div>
							<i class="el-icon-jingjirenjinglikehumianxingcopy"></i>
							<el-dropdown>
								<span class="el-dropdown-link">
									个人中心<i class="el-icon-arrow-down el-icon--right"></i>
								</span>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item icon="el-icon-video-camera-solid" @click.native="open">设备管理</el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-s-check" @click.native="juese">角色管理</el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-eleme" @click.native="zhanghao">用户管理</el-dropdown-item>
                                    <el-dropdown-item icon="el-icon-tickets" @click.native="journal">日志管理</el-dropdown-item>
									<el-dropdown-item icon="el-icon-tuichudenglu" @click.native="tuichu">退出登录</el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
						</div>
					</div>
                </el-header>
                <el-main>
                    <Crumbs></Crumbs>
                    <router-view />
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<style scoped>
    #app{
        width: 100vw;
        height: 100vh;
        overflow: auto;
        position: relative;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
        height: 100vh;
    }
    .el-menu--collapse {
        height: 100vh;
    }
    .logo {
        height: 60px;
        position: relative;
    }
    .logobox {
        width: 200px;
        position: absolute;
        bottom: 0;
        overflow: hidden;
    }
    .logo img {
        width: 64px;
    }
    body,
    html {
        width: 100%;
        height: 100%;
    }
    .el-menu-item,
    .el-submenu,
    .el-submenu__title {
        color: #fff;
    }
    .el-submenu__title span{
        color: #fff;
    }
    .el-aside .el-menu a {
        display: block;
    }
    #app {
        height: 100%;
    }
    .el-header,
    .el-footer {
        background-color: #f3f3f3;
        color: #333;
        text-align: center;
        line-height: 60px;
        width: 100%;
        height: 10%;
    }

    .el-container {
        height: 100vh;
    }
    .el-main {
        color: #333;
        height: 100%;
        overflow: auto;
    }
    .el-menu-item{
        background-color: #000;
    }

    body > .el-container {
        margin-bottom: 40px;
    }
    .el-header{
        position: relative;
    }
    #fll {
        position: absolute;
        top: 5px;
        left: 0px;
        width: 50px;
        height: 50px;
        cursor: pointer;
		
    }
    /* .el-dropdown{
        margin-right: 20px;
    }
    .el-dropdown-link {
        cursor: pointer;
        color: #909399;
    }
    .el-icon-arrow-down {
        font-size: 12px;
    } */
	.header-right{
		float: right;
	}
	.header-right>div{
		float: left;
		color: #909399;
		height: 60px;
		line-height: 60px;
		padding: 0 20px;
		cursor: pointer;
	}
	.el-dropdown-link:hover{
		color: #303133;
	}
	.header-right i{
		font-size: 15px;
		line-height: 60px;
		margin-right: 5px;
	}
</style>
<script>
    import Crumbs from '@/components/Crumbs.vue'
    export default {
        data() {
            return {
                isCollapse: false,
                table: [],
                president:false,
                Name:'',
                ROLEID:''
            };
        },
        components:{
          Crumbs
        },
        methods: {
            dsplins() {
                if (!this.isCollapse) {
                    this.$refs.fll.className = "el-icon-sanhengxian";
                    this.isCollapse = true;
                } else {
                    this.$refs.fll.className = "el-icon-sanhengxian1";
                    this.isCollapse = false;
                }
            },
            open(){
                this.$router.push({name:'device'})
            },
			tuichu(){
                this.$router.push({name:'login'})
                sessionStorage.removeItem('SessionKey')
			},
            zhanghao(){
                if(this.president){
                    this.$router.push({name:'user'})
                }else{
                    this.$message('您暂无此权限！')
                }
            },
            juese(){
                if(this.president){
                    this.$router.push({name:'role'})
                }else{
                    this.$message('您暂无此权限！')
                }
            },
            journal(){
                if(this.president){
                    this.$router.push({name:'journal'})
                }else{
                    this.$message('您暂无此权限！')
                }
            }
        },
        mounted(){
            this.table = this.$router.options.routes[1].children;
            this.Name = sessionStorage.getItem("Name")
            this.ROLEID = sessionStorage.getItem("ROLEID")
            if(this.ROLEID == 1){
                this.president = true
            }else{
                this.president = false
            }
        },
        computed:{
            isshowList:function(){
                return this.table.filter(function (user) {
                    return user.meta.isshow&&!user.children
                })
            },
            isshowLists:function(){
                return this.table.filter(function (user) {
                    return user.meta.isshow&&user.children
                })
            }
        }
    }
</script>